import { Component, EventEmitter, HostBinding, Input, Output, ViewEncapsulation } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { DscBadgeDirective } from 'sidsc-components/dsc-badge';

@Component({
  selector: 'dsc-button-header',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [CommonModule, MatIconModule, DscBadgeDirective],
  templateUrl: './dsc-button-header.component.html',
  styleUrls: ['./dsc-button-header.component.scss']
})
export class DscButtonHeaderComponent {

  @Input() icon?: string;

  @Input() label?: string;

  @Input() tabIndex: number = 0;

  @Input() dscTooltip?: string;

  @Input() ariaLabel?: string;

  @Input()
  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: boolean | string) {
    this._disabled = coerceBooleanProperty(value);
  }

  private _disabled = false;

  @HostBinding('attr.disabled')
  get isDisabled() {
    return this.disabled ? true : null;
  }

  @Output() buttonClick: EventEmitter<void> = new EventEmitter();

  onClick(): void {
    if (!this.disabled) {
      this.buttonClick.emit();
    }
  }

}
